export default ".slick-slider {\n  width: 100%;\n  margin: auto;\n}\n\n.card_style {\n  background-color: rgba(0, 0, 0, 0);\n  border: 1px solid white;\n  color: white;\n  border-radius: 2%;\n  text-align: center;\n}\n\n.wrapper {\n  position: relative;\n  width: 100%;\n}\n\n.wrapper::after {\n  position: absolute;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  content: \"\";\n  background: linear-gradient(0deg, #050505 10%, rgba(48, 48, 48, 0.5) 40%, rgba(0, 0, 0, 0) 100%);\n}\n\n.accor {\n  background-color: rgba(0, 0, 0, 0) !important;\n}\n\n.slick-next .slick-arrow {\n  visibility: hidden !important;\n  background: black !important;\n}\n\n.timeline-centered {\n  color: #CCD5DB;\n}\n\n@media (min-width: 992px) {\n  .timeline-centered,\n.timeline-centered .timeline-item,\n.timeline-centered .timeline-info,\n.timeline-centered .timeline-marker,\n.timeline-centered .timeline-content {\n    display: block;\n    margin: 0;\n    padding: 0;\n  }\n  .timeline-centered .timeline-item {\n    padding-bottom: 40px;\n    overflow: hidden;\n  }\n  .timeline-centered .timeline-marker {\n    position: absolute;\n    left: 50%;\n    margin-left: -7.5px;\n  }\n  .timeline-centered .timeline-info,\n.timeline-centered .timeline-content {\n    width: 50%;\n  }\n  .timeline-centered > .timeline-item:nth-child(odd) .timeline-info {\n    float: left;\n    text-align: right;\n    padding-right: 30px;\n  }\n  .timeline-centered > .timeline-item:nth-child(odd) .timeline-content {\n    float: right;\n    text-align: left;\n    padding-left: 30px;\n  }\n  .timeline-centered > .timeline-item:nth-child(even) .timeline-info {\n    float: right;\n    text-align: left;\n    padding-left: 30px;\n  }\n  .timeline-centered > .timeline-item:nth-child(even) .timeline-content {\n    float: left;\n    text-align: right;\n    padding-right: 30px;\n  }\n  .timeline-centered > .timeline-item.period .timeline-content {\n    float: none;\n    padding: 0;\n    width: 100%;\n    text-align: center;\n  }\n  .timeline-centered .timeline-item.period {\n    padding: 50px 0 90px;\n  }\n  .timeline-centered .period .timeline-marker:after {\n    height: 30px;\n    bottom: 0;\n    top: auto;\n  }\n  .timeline-centered .period .timeline-title {\n    left: auto;\n  }\n}\n\n/*==================================\n    TIMELINE\n==================================*/\n\n/*-- GENERAL STYLES\n------------------------------*/\n\n.timeline {\n  color: white !important;\n  line-height: 1.4em;\n  list-style: none;\n  margin: 0;\n  padding: 0;\n  width: 100%;\n}\n\n.timeline h1, .timeline h2, .timeline h3, .timeline h4, .timeline h5, .timeline h6 {\n  line-height: inherit;\n}\n\n/*----- TIMELINE ITEM -----*/\n\n.timeline-item {\n  padding-left: 40px;\n  position: relative;\n}\n\n.timeline-item:last-child {\n  padding-bottom: 0;\n}\n\n/*----- TIMELINE INFO -----*/\n\n.timeline-info {\n  font-size: 12px;\n  font-weight: 700;\n  letter-spacing: 3px;\n  margin: 0 0 0.5em 0;\n  text-transform: uppercase;\n  white-space: nowrap;\n}\n\n/*----- TIMELINE MARKER -----*/\n\n.timeline-marker {\n  position: absolute;\n  top: 0;\n  bottom: 0;\n  left: 0;\n  width: 15px;\n}\n\n.timeline-marker:before {\n  background: #e2dddd;\n  border: 3px solid transparent;\n  border-radius: 100%;\n  content: \"\";\n  display: block;\n  height: 15px;\n  position: absolute;\n  top: 4px;\n  left: 0;\n  width: 15px;\n  transition: background 0.3s ease-in-out, border 0.3s ease-in-out;\n}\n\n.timeline-marker:after {\n  content: \"\";\n  width: 3px;\n  background: #CCD5DB;\n  display: block;\n  position: absolute;\n  top: 24px;\n  bottom: 0;\n  left: 6px;\n}\n\n.timeline-item:last-child .timeline-marker:after {\n  content: none;\n}\n\n.timeline-item:not(.period):hover .timeline-marker:before {\n  background: transparent;\n  border: 3px solid #e2dddd;\n}\n\n/*----- TIMELINE CONTENT -----*/\n\n.timeline-content {\n  padding-bottom: 40px;\n}\n\n.timeline-content p:last-child {\n  margin-bottom: 0;\n}\n\n/*----- TIMELINE PERIOD -----*/\n\n.period {\n  padding: 0;\n}\n\n.period .timeline-info {\n  display: none;\n}\n\n.period .timeline-marker:before {\n  background: transparent;\n  content: \"\";\n  width: 15px;\n  height: auto;\n  border: none;\n  border-radius: 0;\n  top: 0;\n  bottom: 30px;\n  position: absolute;\n  border-top: 3px solid #CCD5DB;\n  border-bottom: 3px solid #CCD5DB;\n}\n\n.period .timeline-marker:after {\n  content: \"\";\n  height: 32px;\n  top: auto;\n}\n\n.period .timeline-content {\n  padding: 40px 0 70px;\n}\n\n.period .timeline-title {\n  color: #CCD5DB;\n  margin: 0;\n}\n\n.accordion {\n  color: #FFFFFF;\n  cursor: pointer;\n  padding: 18px;\n  width: 100%;\n  border: none;\n  text-align: left;\n  outline: none;\n  border-color: #FFFFFF;\n  margin: 0;\n  padding: 15px 20px;\n  font-weight: 200;\n  line-height: 1;\n  background-color: #35373898;\n  border-radius: 0px 0px 0px 0px;\n  font-size: 1em;\n  transition: 0.4s;\n  font-family: SFMono-Regular, Menlo, Monaco, Consolas, \"Liberation Mono\", \"Courier New\", monospace;\n}\n\n.active, .accordion:hover {\n  background-color: rgba(2, 2, 2, 0);\n  color: #e5eaeb;\n}\n\n.mainpage-button__soon:hover {\n  background-color: #565757;\n}\n\n.accordion:after {\n  color: #FFFFFF;\n  font-weight: bold;\n  float: right;\n  margin-left: 5px;\n  font-size: \"9rem\";\n}\n\n.active:after {\n  color: #909697;\n}\n\n.mainpage_accordion__title {\n  color: #FFFFFF;\n  font-size: 1.2em;\n  font-weight: 400;\n  padding: 30px 30px 30px 30px;\n}\n\n.panel {\n  padding: 2px 18px;\n  background-color: #1B91EF00;\n  max-height: 0;\n  border-top: none;\n  overflow: hidden;\n  transition: max-height 0.2s ease-out;\n}\n\n@media only screen and (max-width: 600px) {\n  .al {\n    text-align: center;\n    align-content: center;\n  }\n}\n\n@media only screen and (max-width: 600px) {\n  .btn {\n    text-align: center;\n    margin: auto;\n    align-items: center;\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */";